div.business-card {
  height: 427px;
  width: 320px;
  margin-left: -160px;
  margin-top: -213.5px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-perspective: 1000;
  perspective: 1000;
}
div.business-card:hover .flipper, div.business-card.hover .flipper {
  -webkit-transform: rotateY(180deg) rotateZ(90deg);
  transform: rotateY(180deg) rotateZ(90deg);
}

div.flipper {
  transition: 0.6s;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  position: relative;
  -webkit-transform-origin: center 160px;
  transform-origin: center 160px;
}

div.front, div.back {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  height: 427px;
  width: 320px;
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.75);
}

div.front {
  background-color: white;
  z-index: 0;
}
div.front:before, div.front:after {
  display: block;
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  border-width: 160px;
  border-style: solid;
  border-color: transparent;
}
div.front:before {
  border-top-color: #288fbd;
  border-left-color: #288fbd;
  z-index: 2;
}
div.front:after {
  border-top-color: #005d8c;
  border-right-color: #005d8c;
}
div.front div.name {
  position: absolute;
  bottom: 8.5px;
  left: 85px;
}
div.front div.name span {
  display: block;
  font-size: 40px;
  line-height: 45px;
}
div.front div.name span.first {
  color: #005d8c;
}
div.front div.name span.last {
  color: #288fbd;
}
div.front div.name span.title {
  font-size: 20px;
  line-height: 20px;
}

div.back {
  background-color: #005d8c;
  color: white;
  width: 427px;
  height: 320px;
  box-sizing: border-box;
  -webkit-transform: rotateY(180deg) rotateZ(90deg);
  transform: rotateY(180deg) rotateZ(90deg);
}
div.back div.container-sm {
  float: left;
  width: 40%;
  height: 100%;
  position: relative;
}
div.back div.container-lg {
  float: left;
  width: 60%;
  margin-top: 2rem;
}
div.back figure.logo-white {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 120px;
  height: 120px;
  display: block;
  margin: -60px -60px;
}
div.back figure.logo-white:before, div.back figure.logo-white:after {
  display: block;
  content: '';
  position: absolute;
  border-style: solid;
  border-color: transparent;
}
div.back figure.logo-white:before {
  top: 0;
  left: 0;
  border-width: 60px;
  border-top-color: white;
  border-left-color: white;
  z-index: 2;
}
div.back figure.logo-white:after {
  bottom: 0;
  right: 0;
  border-width: 56px;
  border-right-color: white;
}
div.back a {
  color: white;
  text-decoration: none;
  display: block;
}
div.back a:hover {
  text-decoration: underline;
}
div.back ul.social {
  font-size: 1.5rem;
}
div.back ul.social li {
  margin-top: 1rem;
}
div.back ul.social li:first-child {
  margin-top: 6rem;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section, main {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

*, *::after, *::before {
  box-sizing: border-box;
}

